header {
    height: 100vh;
    overflow: hidden;
    padding-top: 7rem;
}
.container.container__header {
    display: flex;
    flex-direction: column;
    width: 50%;
}
.container__header h5{
    margin-top: 13rem;
    font-size: 1rem;
    font-family: 'Roboto Mono', monospace;
    color: var(--color-primary);
}
.container__header h2,h3{
    font-weight: 700;
    font-size: 4rem;
    margin-bottom: -1.8rem;
} 
.container__header h2{
    color: var(--color-white);
}
.header__socials {
    display: flex;
    flex-direction: column;
    position: fixed;
    left: 3rem;
    font-size: 1.5rem;
    bottom: 0;
    gap: 1rem;
    align-items: center;
}
.header__socials::after {
    content: '';
    width: 1px;
    height: 6rem;
    background: var(--color-light);
}
.header__socials a{
    transform: translateY(0);
    transition: var(--transition);
}
.header__socials a:hover{
    transform: translateY(-.3rem);
}
.container__header p{
    margin-top: 2rem;
    width: 65%;
}
.header-btn {
    display: inline-block;
    margin-top: 2rem;
}
.emailFixed {
    font-family: 'Roboto Mono', monospace;
    font-size: .9rem;
    position: fixed;
    right: -3rem;
    bottom: 7rem;
    rotate: calc(90deg);
    display: flex;
    align-items: center;
    gap: 3.5rem;
    transform: translateX(0);
    transition: var(--transition);
}
.emailFixed::after {
    content: '';
    background: var(--color-light);
    height: 6rem;
    width: 1px;
    rotate: calc(90deg);

}
.emailFixed:hover {
    transform: translateX(-.3rem);
}
