#preLoader{

    width: 100%;

    height: 100%;

    position: absolute;

    top: 0;

    left: 0;

background: var(--color-bg);

    z-index: 10000;

    overflow: hidden;
    fill: var(--color-primary);

}

.preLoader-logo{

    position: absolute;

    top: 50%;

    left: 50%;

    transform: translate(-50%, -50%);

}

#X{

    animation: animateOne -0.3s linear 1s infinite alternate;
    background: var(--color-bg);
}
#C{

    animation: animateOne -0.4s linear 1s infinite alternate;
    color: var(--color-primary);


}
#O{

    animation: animateTwo -0.5s linear 1s infinite alternate;
    color: var(--color-primary);


}
#D{

    animation: animateTwo -0.6s linear 1s infinite alternate;
    color: var(--color-primary);


}
#N{

    animation: animateOne -0.7s linear 1s infinite alternate;
    color: var(--color-primary);


}
#I{

    animation: animateOne -0.8s linear 1s infinite alternate;
    color: var(--color-primary);


}
#F{

    animation: animateOne -0.9s linear 1s infinite alternate;
    color: var(--color-primary);


}





@keyframes animateOne {

    0%{

        opacity: 0;


    }

    100%{

        opacity: 1;



    }

}

@keyframes animateTwo {

    0%{

        opacity: 0;



    }

    100%{

        opacity: 1;



    }

}