.container.container__about {
    width: 45%;
}
.container__about {
    padding-top: 13rem;
    display: grid;
    grid-template-columns: 60% 30%;
    gap: 7%;
    justify-content: center;
}
.container__img-about {
    margin-top: 6rem;
    width: 300px;
    aspect-ratio: 1/1;
    border-radius: .5rem;
    background: transparent;
    border: 3px solid var(--color-primary);
    display: grid;
    place-items: center;
    z-index: 2;
}
.about__me-img::before {
    background-color: rgba(100, 255, 218, 0.3);
    content: "";
    height: 100%;
    left: 0;
    position: absolute;
    top: 0;
    width: 100%;
    transition: var(--transition);
}
.about__me-img:hover::before {
    background-color: transparent;
}
.about__me-img {
    border-radius: .5rem;
    overflow: hidden;
    transition: var(--transition);
    z-index: 1;
    position: relative;
    transform: translate(-1rem, -1rem);

}
.about__me-img:hover {
    transform: translate(-.7rem, -.7rem);
}
.contain-h2 h2 {
    align-items: center;
    display: flex;
    font-size: 2rem;
    color: var(--color-white);
    gap: 1rem;
    margin-bottom: 2rem;
}
.contain-h2 h2::after {
    content: '';
    height: 1px;
    width: 14rem;
    background: var(--color-light);
}
.contain-h2 h2::before {
    font-size: 1.2rem;
    font-family: 'Roboto Mono', monospace;
    content: '01. ';
    color: var(--color-primary);
}
.skillList {
    margin-top: 1rem;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: .5rem;
}
.skillList li::before {
    content: "▹ ";
    color: var(--color-primary);
    margin-left: .4rem;
}