nav {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 1000;
    background-color: var(--color-bg);
    transition: var(--transition);
  }  
.navigation {
    padding: 0;
    margin: 0 3rem;
    display: flex;
    justify-content: space-between;
}
.logo {
    display: block;
    flex-basis: 63%;
}
.logo_container {
    margin-top: 1.3rem;
    display: flex;
}
.logo a {
    font-size: 3rem;
    color: var(--color-primary);
}
.navName {
    display: flex;
}
.navName ul {
    display: flex;
    justify-content: space-around;
    gap: 1.5rem;
    align-items: center;
    font-weight: 300;
    font-size: .8rem;
    font-family: 'Roboto Mono', monospace;
}
.navName ul a {
    color: var(--color-white);
}
.navName ul li a span{
    color: var(--color-primary);
}
.navName ul li a.active{
    color: var(--color-primary);
}
.navName ul a:hover {
    color: var(--color-primary);
}
